import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/app/guards/auth.guard';

import { MisionDetailComponent } from './misionDetail/misionDetail.component';
import { MisionsComponent } from './misions/misions.component';
import { MisionUserDetailComponent } from './misionUserDetail/misionUserDetail.component';
import { OrganizationComponent } from './organization/organization.component';
import { OrganizationDetailComponent } from './organizationDetail/organizationDetail.component';
import { PagesComponent } from './pages.component';
import { PlayerComponent } from './player/player.component';
import { TableMisionsComponent } from './tableMisions/tableMisions.component';

const routes: Routes = [
  {
    path: 'menu',
    component: PagesComponent,
    canActivate: [AuthGuard],
    children: [
      { path: 'player', component: PlayerComponent },
      { path: 'misions', component: MisionsComponent },
      { path: 'table', component: TableMisionsComponent },
      { path: 'misionDetail', component: MisionDetailComponent },
      { path: 'misionUserDetail', component: MisionUserDetailComponent },
      { path: 'organization', component: OrganizationComponent },
      { path: 'organizationDetail', component: OrganizationDetailComponent },
    ]
  },
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ]
})
export class PagesRoutingModule{}
