import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MisionService } from 'src/app/services/mision.service';
import { MisionUserService } from 'src/app/services/misionUser.service';
import { Mision } from '../../models/mision.model';
import { MisionUser } from '../../models/misionUser.model';

@Component({
  selector: 'app-misions',
  templateUrl: './misions.component.html',
  styleUrls: ['./misions.component.css']
})
export class MisionsComponent implements OnInit {

  misionUser: MisionUser[] = [];
  misionList: Mision[] = [];
  constructor(
    private misionUserService: MisionUserService,
    private misionService: MisionService,
    private router: Router
  ) { }

  ngOnInit(): void {
    const idUser = sessionStorage.getItem('uId');
    this.misionUserService.getMisionById(idUser)
      .subscribe(resp => {
        this.misionUser = resp;
        this.getAllMisionUser();
        console.log(resp);
      }, (err) => {
      });
  }

  getAllMisionUser = () => {
    this.misionUser.forEach(mision => {
      this.misionService.getMisionById(mision.idMision)
        .subscribe(res => {
          this.misionList.push(res);
        });
    });
  }

  misionDetail = (mision)  => {
    this.misionUserService.setMision(mision);
    this.router.navigateByUrl('menu/misionUserDetail');
  }

}
