import { Component, OnInit } from '@angular/core';
import { MisionService } from 'src/app/services/mision.service';
import { MisionUserService } from 'src/app/services/misionUser.service';

import { Mision } from '../../models/mision.model';
import { MisionUser } from '../../models/misionUser.model';

@Component({
  selector: 'app-misionDetail',
  templateUrl: './misionDetail.component.html',
  styleUrls: ['./misionDetail.component.css']
})
export class MisionDetailComponent implements OnInit {

  misionDetail: Mision = new Mision('', '', '', '', '', '', '', 0, 0, '', '');
  misionUser: MisionUser = new MisionUser('', '', false);

  constructor(
    private misionService: MisionService,
    private misionUserService: MisionUserService
  ) { }

  ngOnInit(): void {
    const misionId = localStorage.getItem('misionId');
    this.misionService.getMisionById(misionId)
      .subscribe(res => {
        this.misionDetail = res;
      });
  }

  createMisionUser = () => {
    this.misionUser.idUser = sessionStorage.getItem('uId');
    this.misionUser.idMision = localStorage.getItem('misionId');
    this.misionUser.misionActive = true;
    this.misionUserService.createUserMision(this.misionUser)
      .subscribe(res => {
        console.log(res);
      });
  }

}
