import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MisionService } from 'src/app/services/mision.service';
import { MisionUserService } from 'src/app/services/misionUser.service';
import { Mision } from '../../models/mision.model';
import { MisionUser } from '../../models/misionUser.model';

@Component({
  selector: 'app-tableMisions',
  templateUrl: './tableMisions.component.html',
  styleUrls: ['./tableMisions.component.css']
})
export class TableMisionsComponent implements OnInit {

  misionList: Mision[] = [];
  misionListR: Mision[] = [];
  misionListE: Mision[] = [];
  misionUser: MisionUser[] = [];
  constructor(
    private misionService: MisionService,
    private misionUserService: MisionUserService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.obtainMisionUser();
    this.misionService.getAllMision()
      .subscribe(resp => {
        this.misionUser.forEach((misionU) => {
          resp.forEach((misionR: any, indexR) => {
            if (misionU.idMision === misionR._id) {
              delete resp[indexR];
            }
          });
        });

        this.misionList = resp.filter((el) => {
          return el != null;
        });
        this.orginizeMision(this.misionList);
      }, (err) => {
        console.log(err);
      });
  }

  orginizeMision = (misions: Mision[]) => {
    misions.forEach(mision => {
      const typeMision = mision.category;
      if (typeMision === 'Reconocimiento') {
        this.misionListR.push(mision);
      } else if (typeMision === 'Entrenamiento') {
        this.misionListE.push(mision);
      }
    });
  }

  selectMision = (mision: any) => {
    localStorage.setItem('misionId', mision._id);
    this.router.navigateByUrl('menu/misionDetail');
  }

  obtainMisionUser = () => {
    const userId = sessionStorage.getItem('uId');
    this.misionUserService.getMisionById(userId)
      .subscribe(resp => {
        this.misionUser = resp;
      });
  }

}
