import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

import { Mision } from '../components/models/mision.model';

const baseUrl = environment.base_url;

@Injectable({
  providedIn: 'root'
})
export class MisionService {
  token: string;
  constructor(private http: HttpClient) {
    this.token = sessionStorage.getItem('token');
  }

  getAllMision = () => {
    // const token = sessionStorage.getItem('token');
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('x-token', this.token);
    return this.http.get(`${baseUrl}/mision`, { headers })
      .pipe(
        map((resp: Mision[]) => resp)
      );
  }

  getMisionById = (id: string) => {
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('x-token', this.token);
    return this.http.get(`${baseUrl}/mision/${id}`, { headers })
      .pipe(
        map((resp: Mision) => resp)
      );
  }

}
