import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Organization } from '../components/models/organization.model';

const baseUrl = environment.base_url;

@Injectable({
  providedIn: 'root'
})
export class OrganizationService {
  token: string;
  // organization: Organization;
  organizationDetail: Organization;

  constructor(private http: HttpClient) {
    this.token = sessionStorage.getItem('token');
  }
  getAllOrganizations = () => {
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('x-token', this.token);
    return this.http.get(`${baseUrl}/organization`, { headers })
      .pipe(
        map((resp: Organization[]) => resp)
      );
  }

  setOrganization = (organization: Organization) => {
    this.organizationDetail = organization;
  }

  getOrganization = () => {
    return this.organizationDetail;
  }
}
