import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

import { Mision } from '../components/models/mision.model';
import { MisionUser } from '../components/models/misionUser.model';


const baseUrl = environment.base_url;

@Injectable({
  providedIn: 'root'
})


export class MisionUserService {
  token: string;
  misionDetailUser: Mision;

  constructor(private httpClient: HttpClient) {
    this.token = sessionStorage.getItem('token');
  }

  createUserMision = (misionUser: MisionUser) => {
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('x-token', this.token);
    return this.httpClient.post(`${baseUrl}/misionUser`, misionUser, { headers });
  }

  getMisionById = (id: string) => {
    let headers: HttpHeaders = new HttpHeaders();
    headers = headers.append('x-token', this.token);
    return this.httpClient.get(`${baseUrl}/misionUser/${id}`, { headers })
      .pipe(
        map((resp: MisionUser[]) => resp)
      );
  }

  setMision = (mision: Mision) => {
    this.misionDetailUser = mision;
  }

  getMision = () => {
    return this.misionDetailUser;
  }

}
