import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pages',
  templateUrl: './pages.component.html',
  styleUrls: ['./pages.component.css']
})
export class PagesComponent implements OnInit {
  hide = true;
  title: string;
  constructor(private router: Router) {
    this.title = 'Heroicap';
  }

  ngOnInit(): void {
    this.router.navigateByUrl('menu/player');
  }
  menuHide = (): void => {
    if (this.hide) {
      this.showMenu();
    } else {
      this.hideMenu();
    }

  }

  showMenu = () => {
    const item = document.getElementById('menu-panel');
    item.animate([{
      transform: 'translatex(-260px)',
      maxWidth: '0px',
      minWidth: '0px'
    },
    {
      transform: 'translatex(0)',
      maxWidth: '260px',
      minWidth: '260px'
    }
    ], {
      duration: 100,
      // easing: 'ease-in-out',
      iterations: 1,
      direction: 'alternate',
      fill: 'forwards'
    });
    this.hide = false;
  }

  hideMenu = () => {
    this.hide = true;
    const item = document.getElementById('menu-panel');
    item.animate([{
      transform: 'translatex(0)',
      maxWidth: '260px',
      minWidth: '260px'
    },
    {
      transform: 'translatex(-260px)',
      maxWidth: '0px',
      minWidth: '0px'
    }
    ], {
      duration: 100,
      // easing: 'ease-in-out',
      iterations: 1,
      direction: 'alternate',
      fill: 'forwards'
    });
  }

}
